import React, { useState } from 'react';
import { 
  Box, 
  Button, 
  FormControl, 
  FormLabel, 
  InputLabel,
  MenuItem,
  Select,
  TextField,
  Typography 
} from '@mui/material';
import { tokyoAirports } from '../data/japanData';
import { Autocomplete } from '@react-google-maps/api';

//    - 
const DestinationSelector = ({ selectedDestinations, onDestinationToggle }) => {
  const keywords = [
    { id: 'shinjuku', text: '' },
    { id: 'shibuya', text: '' },
    { id: 'harajuku', text: '' },
    { id: 'ginza', text: '' }
  ];

  return (
    <Box sx={{ 
      display: 'flex', 
      gap: '20px', 
      mt: '20px', 
      justifyContent: 'center',
      flexWrap: 'wrap',
      maxWidth: '600px'
    }}>
      {keywords.map((keyword) => (
        <Box
          key={keyword.id}
          onClick={() => onDestinationToggle(keyword.id)}
          sx={{
            width: '120px',
            height: '35px',
            border: '1px solid #00b7d0',
            borderRadius: '16px',
            backgroundColor: selectedDestinations.includes(keyword.id) ? '#00b7d0' : 'rgba(255, 255, 255, 0.8)',
            fontFamily: 'Pretendard, sans-serif',
            fontSize: '16px',
            fontWeight: 600,
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            cursor: 'pointer',
            transition: 'all 0.3s ease',
            color: selectedDestinations.includes(keyword.id) ? '#ffffff' : '#333'
          }}
        >
          {keyword.text}
        </Box>
      ))}
    </Box>
  );
};

//    -  
const DurationSelector = ({ selectedDuration, setDuration, onAutoNext }) => {
  const keywords = [
    { id: '1day', text: '' },
    { id: '1night', text: '1 2' },
    { id: '2night', text: '2 3' },
    { id: '3night', text: '3 4' },
    { id: '4night', text: '4 5' },
    { id: '5night', text: '5 6' }
  ];

  return (
    <Box sx={{ 
      display: 'flex', 
      gap: '20px', 
      mt: '20px', 
      justifyContent: 'center',
      flexWrap: 'wrap',
      maxWidth: '600px'
    }}>
      {keywords.map((keyword) => (
        <Box
          key={keyword.id}
          onClick={() => {
            setDuration(keyword.id);
            if (onAutoNext) onAutoNext();
          }}
          sx={{
            width: '120px',
            height: '35px',
            border: '1px solid #00b7d0',
            borderRadius: '16px',
            backgroundColor: selectedDuration === keyword.id ? '#00b7d0' : 'rgba(255, 255, 255, 0.8)',
            fontFamily: 'Pretendard, sans-serif',
            fontSize: '16px',
            fontWeight: 600,
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            cursor: 'pointer',
            transition: 'all 0.3s ease',
            color: selectedDuration === keyword.id ? '#ffffff' : '#333'
          }}
        >
          {keyword.text}
        </Box>
      ))}
    </Box>
  );
};

