//    -  
const DensitySelector = ({ scheduleDensity, setScheduleDensity, onAutoNext }) => {
  const keywords = [
    { id: 'tight', text: '' },
    { id: 'relaxed', text: '' }
  ];

  return (
    <Box sx={{ 
      display: 'flex', 
      gap: '20px', 
      mt: '20px', 
      justifyContent: 'center',
      flexWrap: 'wrap',
      maxWidth: '600px'
    }}>
      {keywords.map((keyword) => (
        <Box
          key={keyword.id}
          onClick={() => {
            setScheduleDensity(keyword.id);
            if (onAutoNext) onAutoNext();
          }}
          sx={{
            width: '100%',
            height: '45px',
            border: '2px solid #00b7d0',
            borderRadius: '16px',
            backgroundColor: scheduleDensity === keyword.id ? '#00b7d0' : 'rgba(255, 255, 255, 0.8)',
            fontFamily: 'Pretendard, sans-serif',
            fontSize: '20px',
            fontWeight: 600,
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            cursor: 'pointer',
            transition: 'all 0.3s ease',
            color: scheduleDensity === keyword.id ? '#ffffff' : '#333'
          }}
        >
          {keyword.text}
        </Box>
      ))}
    </Box>
  );
};

const TravelForm = ({ onSubmit, onBackToHome }) => {
  const [activeStep, setActiveStep] = useState(0);
  const [selectedAirport, setSelectedAirport] = useState('');
  const [arrivalTime, setArrivalTime] = useState('12:00');
  const [departureTime, setDepartureTime] = useState('18:00');
  const [arrivalHour, setArrivalHour] = useState(12);
  const [arrivalMinute, setArrivalMinute] = useState(0);
  const [departureHour, setDepartureHour] = useState(18);
  const [departureMinute, setDepartureMinute] = useState(0);
  const [selectedDuration, setSelectedDuration] = useState('');
  const [people, setPeople] = useState(2);
  const [selectedStyles, setSelectedStyles] = useState([]);
  const [selectedDestinations, setSelectedDestinations] = useState([]);
  const [scheduleDensity, setScheduleDensity] = useState('');
  const [accommodationAddress, setAccommodationAddress] = useState('');
  const [accommodationLocation, setAccommodationLocation] = useState(null);
  const [autocomplete, setAutocomplete] = useState(null);

  //  HH:MM  
  const formatTime = (hour, minute) => {
    return `${String(hour).padStart(2, '0')}:${String(minute).padStart(2, '0')}`;
  };

  //    
  const handleArrivalHourChange = (newHour) => {
    const hour = Math.max(1, Math.min(24, newHour));
    setArrivalHour(hour);
    setArrivalTime(formatTime(hour, arrivalMinute));
  };

  const handleArrivalMinuteChange = (newMinute) => {
    const minute = Math.max(0, Math.min(59, newMinute));
    setArrivalMinute(minute);
    setArrivalTime(formatTime(arrivalHour, minute));
  };

  //    
  const handleDepartureHourChange = (newHour) => {
    const hour = Math.max(1, Math.min(24, newHour));
    setDepartureHour(hour);
    setDepartureTime(formatTime(hour, departureMinute));
  };

  const handleDepartureMinuteChange = (newMinute) => {
    const minute = Math.max(0, Math.min(59, newMinute));
    setDepartureMinute(minute);
    setDepartureTime(formatTime(departureHour, minute));
  };

  const handleStyleToggle = (styleId) => {
    if (selectedStyles.includes(styleId)) {
      setSelectedStyles(selectedStyles.filter(id => id !== styleId));
    } else {
      setSelectedStyles([...selectedStyles, styleId]);
    }
  };

  const handleDestinationToggle = (destinationId) => {
    if (selectedDestinations.includes(destinationId)) {
      setSelectedDestinations(selectedDestinations.filter(id => id !== destinationId));
    } else {
      setSelectedDestinations([...selectedDestinations, destinationId]);
    }
  };

  const validateStep = (step) => {
    if (step === 0) {
      if (!selectedAirport) {
        alert(' .');
        return false;
      }
      if (!arrivalTime || !departureTime) {
        alert('     .');
        return false;
      }
    }
    if (step === 1) {
      if (selectedDestinations.length === 0) {
        alert('   .');
        return false;
      }
    }
    if (step === 2) {
      if (!selectedDuration) {
        alert('  .');
        return false;
      }
    }
    if (step === 3) {
      if (selectedStyles.length === 0) {
        alert('    .');
        return false;
      }
    }
    if (step === 4) {
      if (!scheduleDensity) {
        alert('  .');
        return false;
      }
    }
    return true;
  };

  const handleNext = () => {
    if (!validateStep(activeStep)) return;
    setActiveStep(prev => Math.min(prev + 1, 5));
  };

  const handleBack = () => {
    setActiveStep(prev => Math.max(prev - 1, 0));
  };

  const handleSubmit = () => {
    if (!validateStep(5)) return;
    onSubmit({
      airport: selectedAirport,
      arrivalTime,
      departureTime,
      duration: selectedDuration,
      people,
      styles: selectedStyles,
      destinations: selectedDestinations,
      scheduleDensity,
      accommodation: {
        address: accommodationAddress,
        location: accommodationLocation
      }
    });
  };

  const onLoadAutocomplete = (autocompleteInstance) => {
    setAutocomplete(autocompleteInstance);
  };

  const onPlaceChanged = () => {
    if (autocomplete !== null) {
      const place = autocomplete.getPlace();
      if (place.geometry) {
        setAccommodationAddress(place.formatted_address || place.name);
        setAccommodationLocation({
          lat: place.geometry.location.lat(),
          lng: place.geometry.location.lng(),
          name: place.name,
          placeId: place.place_id
        });
      }
    }
  };

  return (
    <Box sx={{ 
      minHeight: '100vh',
      background: 'linear-gradient(to bottom, rgba(0, 183, 208, 0.5) 0%, #ffffff 100%)',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      fontFamily: 'Pretendard, sans-serif',
      p: 2
    }}>
      <Box sx={{
        width: { xs: '95vw', sm: '900px' },
        height: { xs: 'auto', sm: '1000px' },
        backgroundColor: 'rgba(255, 255, 255, 0.95)',
        borderRadius: '16px',
        boxShadow: '-9px 12px 76px rgba(0, 0, 0, 0.2)',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        flexDirection: 'column',
        border: '1px solid #dddddd',
        p: { xs: 2, sm: 0 }
      }}>
        <Box sx={{
          width: { xs: 'calc(100% - 40px)', sm: 'calc(100% - 80px)' },
          height: { xs: 'auto', sm: 'calc(100% - 80px)' },
          backgroundColor: 'rgba(255, 255, 255, 0.8)',
          borderRadius: '12px',
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          alignItems: 'center',
          position: 'relative',
          p: { xs: 2, sm: '40px' }
        }}>
          {/* 1/6  */}
          <Typography sx={{
            position: 'absolute',
            top: '40px',
            right: '40px',
            fontFamily: 'Pretendard, sans-serif',
            fontWeight: 700,
            fontSize: '24px',
            color: '#333'
          }}>
            {`${activeStep + 1}/6`}
          </Typography>

          {/*   */}
          <Box 
            component="img" 
            src="/images/arrow.png" 
            alt=" "
            onClick={() => activeStep > 0 && handleBack()}
            sx={{
              position: 'absolute',
              bottom: '40px',
              left: '40px',
              width: '40px',
              height: 'auto',
              transform: 'scaleX(-1)',
              cursor: activeStep > 0 ? 'pointer' : 'not-allowed',
              opacity: activeStep > 0 ? 1 : 0.3,
              transition: 'all 0.3s ease'
            }}
          />
          {activeStep < 5 && (
            <Box 
              component="img" 
              src="/images/arrow.png" 
              alt=" "
              onClick={() => {
                if (validateStep(activeStep)) {
                  handleNext();
                }
              }}
              sx={{
                position: 'absolute',
                bottom: '40px',
                right: '40px',
                width: '40px',
                height: 'auto',
                cursor: 'pointer',
                transition: 'all 0.3s ease',
                '&:hover': {
                  transform: 'scale(1.1)'
                }
              }}
            />
          )}

          {/*      */}
          {activeStep === 5 && (
            <Button
              onClick={handleSubmit}
              sx={{
                position: 'absolute',
                bottom: '40px',
                left: '50%',
                transform: 'translateX(-50%)',
                width: '200px',
                height: '50px',
                backgroundColor: '#00b7d0',
                color: '#ffffff',
                borderRadius: '8px',
                fontFamily: 'Pretendard, sans-serif',
                fontWeight: 600,
                fontSize: '18px',
                '&:hover': {
                  backgroundColor: '#008a9e'
                }
              }}
            >
               
            </Button>
          )}

          {/*   */}
          <Box sx={{
            width: { xs: '100%', sm: '600px' },
            height: { xs: 'auto', sm: '730px' },
            backgroundColor: 'rgba(255, 255, 255, 0.9)',
            borderRadius: '8px',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            p: { xs: 2, sm: 3 }
          }}>
            {/*    */}
            <Box sx={{ display: 'flex', justifyContent: 'center', mb: 1 }}>
              <Box component="img" src={'/images/' + (activeStep + 2) + '.png'} alt={' ' + (activeStep + 1)} sx={{ height: 122 }} />
            </Box>
            
            <Typography variant="h5" sx={{ mb: 1.5, textAlign: 'center', fontWeight: 700, fontSize: '30px' }}>
              {activeStep === 0 && '   '}
              {activeStep === 1 && ' ?'}
              {activeStep === 2 && ' '}
              {activeStep === 3 && '  '}
              {activeStep === 4 && ' '}
              {activeStep === 5 && '  ( )'}
            </Typography>
            
            {/*   */}
            <Box sx={{
              width: '600px',
              height: '3px',
              backgroundColor: '#dddddd',
            }} />

            {/*   */}
            {activeStep === 0 && (
              <Box>
                {/*   */}
                <Box sx={{ mb: 4 }}>
                  <Typography variant="h6" sx={{ mb: 2, textAlign: 'center', fontWeight: 600 }}>
                      
                  </Typography>
                  <Box sx={{ display: 'flex', justifyContent: 'center', gap: 2, flexWrap: 'wrap', alignItems: 'stretch' }}>
                    {tokyoAirports.map(airport => {
                      const selected = selectedAirport === airport.id;
                      return (
                        <Box 
                          key={airport.id}
                          onClick={() => setSelectedAirport(airport.id)}
                          sx={{
                            p: 2,
                            border: selected ? '2px solid #1976d2' : '1px solid #ddd',
                            borderRadius: 2,
                            cursor: 'pointer',
                            bgcolor: selected ? '#f3f7ff' : 'white',
                            minWidth: 220,
                            maxWidth: 280,
                            flex: '1 1 200px',
                            textAlign: 'left',
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'space-between',
                            minHeight: 120,
                            height: 120
                          }}
                        >
                          <Typography variant="h6" sx={{ 
                            fontWeight: 600,
                            lineHeight: 1.2
                          }}>
                            {airport.name} ({airport.code})
                          </Typography>
                          <Typography variant="body2" color="text.secondary" sx={{ mt: 0.5 }}>
                            {airport.description}
                          </Typography>
                          <Typography variant="body2" color="primary" sx={{ mt: 0.5 }}>
                            : {airport.accessTime}
                          </Typography>
                        </Box>
                      );
                    })}
                  </Box>
                </Box>

                {/*    */}
                <Box>
                  <Typography variant="h6" sx={{ mb: 2, textAlign: 'center', fontWeight: 600 }}>
                      
                  </Typography>
                  <Box sx={{ display: 'flex', gap: 3, justifyContent: 'center', flexWrap: 'wrap', alignItems: 'flex-start' }}>
                    {/*   */}
                    <Box sx={{ minWidth: 200, maxWidth: 250, flex: '1 1 200px' }}>
                      <Typography variant="subtitle2" sx={{ mb: 1, color: 'text.secondary' }}>
                          ()
                      </Typography>
                      <Box sx={{ display: 'flex', gap: 1, mb: 1 }}>
                        <FormControl sx={{ minWidth: 80 }}>
                          <InputLabel></InputLabel>
                          <Select
                            value={arrivalHour}
                            label=""
                            onChange={(e) => handleArrivalHourChange(e.target.value)}
                          >
                            {Array.from({ length: 24 }, (_, i) => i + 1).map(hour => (
                              <MenuItem key={hour} value={hour}>
                                {hour}
                              </MenuItem>
                            ))}
                          </Select>
                        </FormControl>
                        <FormControl sx={{ minWidth: 80 }}>
                          <InputLabel></InputLabel>
                          <Select
                            value={arrivalMinute}
                            label=""
                            onChange={(e) => handleArrivalMinuteChange(e.target.value)}
                          >
                            {Array.from({ length: 12 }, (_, i) => i * 5).map(minute => (
                              <MenuItem key={minute} value={minute}>
                                {String(minute).padStart(2, '0')}
                              </MenuItem>
                            ))}
                          </Select>
                        </FormControl>
                      </Box>
                      <Typography variant="caption" color="text.secondary">
                           30  
                      </Typography>
                    </Box>

                    {/*   */}
                    <Box sx={{ minWidth: 200, maxWidth: 250, flex: '1 1 200px' }}>
                      <Typography variant="subtitle2" sx={{ mb: 1, color: 'text.secondary' }}>
                          ()
                      </Typography>
                      <Box sx={{ display: 'flex', gap: 1, mb: 1 }}>
                        <FormControl sx={{ minWidth: 80 }}>
                          <InputLabel></InputLabel>
                          <Select
                            value={departureHour}
                            label=""
                            onChange={(e) => handleDepartureHourChange(e.target.value)}
                          >
                            {Array.from({ length: 24 }, (_, i) => i + 1).map(hour => (
                              <MenuItem key={hour} value={hour}>
                                {hour}
                              </MenuItem>
                            ))}
                          </Select>
                        </FormControl>
                        <FormControl sx={{ minWidth: 80 }}>
                          <InputLabel></InputLabel>
                          <Select
                            value={departureMinute}
                            label=""
                            onChange={(e) => handleDepartureMinuteChange(e.target.value)}
                          >
                            {Array.from({ length: 12 }, (_, i) => i * 5).map(minute => (
                              <MenuItem key={minute} value={minute}>
                                {String(minute).padStart(2, '0')}
                              </MenuItem>
                            ))}
                          </Select>
                        </FormControl>
                      </Box>
                      <Typography variant="caption" color="text.secondary">
                           2  
                      </Typography>
                    </Box>
                  </Box>
                </Box>
              </Box>
            )}

            {activeStep === 1 && (
              <Box sx={{ mb: 0.5, width: '100%' }}>
                <DestinationSelector 
                  selectedDestinations={selectedDestinations} 
                  onDestinationToggle={handleDestinationToggle} 
                />
              </Box>
            )}

            {activeStep === 2 && (
              <Box sx={{ width: '100%' }}>
                <DurationSelector 
                  selectedDuration={selectedDuration} 
                  setDuration={setSelectedDuration}
                  onAutoNext={() => {
                    setTimeout(() => setActiveStep(prev => Math.min(prev + 1, 5)), 300);
                  }}
                />
              </Box>
            )}

            {activeStep === 3 && (
              <Box sx={{ mb: 3, width: '100%' }}>
                <FormLabel>   (  )</FormLabel>
                <StyleSelector 
                  selectedStyles={selectedStyles} 
                  onStyleToggle={handleStyleToggle} 
                />
              </Box>
            )}

            {activeStep === 4 && (
              <Box sx={{ mb: 3, width: '100%' }}>
                <FormLabel sx={{ mb: 1, display: 'block', textAlign: 'center' }}>   </FormLabel>
                <DensitySelector 
                  scheduleDensity={scheduleDensity} 
                  setScheduleDensity={setScheduleDensity}
                  onAutoNext={() => {
                    setTimeout(() => setActiveStep(prev => Math.min(prev + 1, 5)), 300);
                  }}
                />
              </Box>
            )}

            {activeStep === 5 && (
              <Box sx={{ mt: 3, width: '100%', display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                <FormLabel sx={{ mb: 2, display: 'block', textAlign: 'center' }}>   ( )</FormLabel>
                <Autocomplete
                  onLoad={onLoadAutocomplete}
                  onPlaceChanged={onPlaceChanged}
                  options={{
                    componentRestrictions: { country: 'jp' },
                    types: ['lodging']
                  }}
                >
                  <TextField
                    fullWidth
                    variant="outlined"
                    placeholder="    "
                    value={accommodationAddress}
                    onChange={(e) => setAccommodationAddress(e.target.value)}
                    sx={{
                      maxWidth: '500px',
                      '& .MuiOutlinedInput-root': {
                        borderRadius: '8px',
                        backgroundColor: 'rgba(255, 255, 255, 0.9)'
                      }
                    }}
                  />
                </Autocomplete>
                {accommodationLocation && (
                  <Box sx={{ mt: 2, p: 2, backgroundColor: 'rgba(0, 183, 208, 0.1)', borderRadius: '8px', maxWidth: '500px', width: '100%' }}>
                    <Typography variant="body2" sx={{ color: '#00b7d0', fontWeight: 600 }}>
                       : {accommodationAddress}
                    </Typography>
                  </Box>
                )}
              </Box>
            )}

            </Box>

          {/*    */}
          <Box sx={{ position: 'absolute', top: '40px', left: '40px' }}>
            <Button 
              size="small" 
              variant="text" 
              onClick={onBackToHome}
              sx={{
                fontSize: '18px',
                color: '#00b7d0',
                fontFamily: 'Pretendard, sans-serif',
                '&:hover': {
                  color: '#008a9e',
                  backgroundColor: 'rgba(0, 183, 208, 0.08)'
                }
              }}
            >
               
            </Button>
          </Box>
        </Box>
      </Box>
    </Box>
  );
};

export default TravelForm;